#backpacks {
  padding-top: 100px;
  .container {
    max-width: 1405px;
    padding-left: 302px;
    padding-top: 60px;
  }

  .section-title {
    margin-bottom: 34px;
    .title {
      font-weight: 200;
      font-size: 48px;
      line-height: 1.3;
      color: #808080;
    }
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -32px;

  &-item {
    width: 27.7%;
    margin: 0 2.8% 50px;

    .media {
      position: relative;
      padding-bottom: 150%;
      display: block;
      margin-bottom: 20px;

      img {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .label {
      font-family: Roboto;
      font-style: normal;
      font-weight: 300;
      font-size: 18px;
      line-height: 1.27;
      color: #303030;
      margin-bottom: 10px;
    }

    .price {
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 140%;
      color: #808080;
    }
  }
}

#secondaryPage {
  .container {
    padding: 160px 15px 0 480px;
    max-width: 1760px;
  }
}

.single-post {
  display: flex;

  .single-post__content {
    padding-left: 60px;
  }

  .title {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 200;
    font-size: 48px;
    line-height: 104%;
    color: #808080;
  }

  .price {
    display: block;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 140%;
    /* or 34px */

    color: #808080;
  }

  .buy {
    background: #f8c018;
    border-radius: 4px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 900;
    font-size: 18px;
    line-height: 32px;
    text-align: center;
    letter-spacing: 0.4em;
    text-transform: uppercase;
    color: #ffffff;
    display: inline-block;
    padding: 30px 50px;
  }

  .delivery-descr {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    color: #808080;
  }

  .descr-block {
    @extend %listreset;
    .title-sm {
      font-family: Roboto;
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      line-height: 140%;
      /* or 25px */
      
      
      color: #303030;
    }

    .descr-sm {
      font-family: Roboto;
font-style: normal;
font-weight: normal;
font-size: 14px;
line-height: 140%;
/* or 20px */


color: #808080;
    }
  }
}
